<template>
  <div>
    <div class="page-title">
      <h3>Личная статистика игрока - {{ $route.params.nick }}</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <Loader v-if="loading" />
      <div v-else>
        <p>Выберите период:</p>
        <select style="display: block" v-model="choosed">
          <option v-for="d of period" :key="d.id">{{ d.title }}</option>
        </select>
        <p>Общая статистика:</p>
        <v-table :data="n">
          <thead slot="head">
            <v-th sortKey="playerName" defaultSort="desc">Ник</v-th>
            <v-th sortKey="totalPlayedGames" defaultSort="desc"
              >Кол-во игр</v-th
            >
            <v-th sortKey="kills" defaultSort="desc">Убийств</v-th>
            <v-th sortKey="teamkills" defaultSort="desc">Тимкиллов</v-th>
            <v-th sortKey="deaths" defaultSort="desc">Смертей</v-th>
            <v-th sortKey="kdRatio" defaultSort="desc">K/D</v-th>
            <v-th sortKey="totalScore" defaultSort="desc">Счет</v-th>
          </thead>
          <tbody slot="body" slot-scope="{ displayData }">
            <tr v-for="row in displayData" :key="row.id">
              <td>
                <router-link
                  :to="{ name: 'Player', params: { nick: row.playerName } }"
                  >{{ row.lastSquadPrefix + row.playerName }}</router-link
                >
              </td>
              <td>{{ row.totalPlayedGames }}</td>
              <td>{{ row.kills }}</td>
              <td>{{ row.teamkills }}</td>
              <td>{{ row.deaths }}</td>
              <td>{{ row.kdRatio }}</td>
              <td>{{ row.totalScore }}</td>
            </tr>
          </tbody>
        </v-table>
        <p>Статистка по неделям:</p>
        <v-table :data="byWeeks">
          <thead slot="head">
            <th>Даты</th>
            <v-th sortKey="totalPlayedGames">Кол-во игр</v-th>
            <v-th sortKey="kills">Убийств</v-th>
            <v-th sortKey="teamkills">Тимкиллов</v-th>
            <v-th sortKey="deaths">Смертей</v-th>
            <v-th sortKey="kdRatio">K/D</v-th>
            <v-th sortKey="totalScore">Счет</v-th>
          </thead>
          <tbody slot="body" slot-scope="{ displayData }">
            <tr v-for="row in displayData" :key="row.id">
              <td>{{ row.startDate }} - {{ row.endDate }}</td>
              <td>{{ row.totalPlayedGames }}</td>
              <td>{{ row.kills }}</td>
              <td>{{ row.teamkills }}</td>
              <td>{{ row.deaths }}</td>
              <td>{{ row.kdRatio }}</td>
              <td>{{ row.totalScore }}</td>
            </tr>
          </tbody>
        </v-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Player",
  data: () => ({
    n: [],
    byWeeks: [],
    loading: false,
    choosed: "За все время",
    period: [
      {
        id: 1,
        title: "За все время",
      },
      { id: 2, title: "За ротацию от 27.02.2022" },
    ],
  }),
  async mounted() {
    await this.refresh();
  },
  watch: {
    choosed: function () {
      this.n = [];
      this.byWeeks = [];
      this.refresh();
    },
  },
  methods: {
    async refresh() {
      if (this.choosed == this.period[0].title) {
        this.r();
      } else if ((this.choosed = this.period[1].title)) {
        this.rotationTable();
      }
    },
    async r() {
      this.loading = true;
      this.n = [];
      this.byWeeks = [];
      let r = await fetch("stats.json");
      r = await r.json();
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].playerName === this.$route.params.nick) {
          if (r.globalStatistics[key].lastSquadPrefix === null) {
            r.globalStatistics[key].lastSquadPrefix = "";
          }
          this.n.push(r.globalStatistics[key]);
          this.byWeeks = r.globalStatistics[key].byWeeks;
        }
      }
      for (let key in this.byWeeks) {
        this.byWeeks[key].totalScore = (
          this.byWeeks[key].kills -
          this.byWeeks[key].teamkills / this.byWeeks[key].totalPlayedGames
        ).toFixed(2);
        this.byWeeks[key].startDate = new Date(
          this.byWeeks[key].startDate
        ).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
        this.byWeeks[key].endDate = new Date(
          this.byWeeks[key].endDate
        ).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
      }
      this.loading = false;
    },
    async rotationTable() {
      this.loading = true;
      this.n = [];
      this.byWeeks = [];
      let r = await fetch("stats.json");
      r = await r.json();
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].playerName === this.$route.params.nick) {
          let newPlayer = {};
          let newOne = [];
          let count = 0,
            kills = 0,
            teamkills = 0,
            deaths = 0;
          for (let i in r.globalStatistics[key].byWeeks) {
            if (r.globalStatistics[key].byWeeks[i].week >= "2022-10") {
              newOne.push(r.globalStatistics[key].byWeeks[i]);
              let m = r.globalStatistics[key].byWeeks[i];
              count += m.totalPlayedGames;
              kills += m.kills;
              teamkills += m.teamkills;
              deaths += m.deaths;
            }
          }
          this.byWeeks = newOne;
          for (let key in this.byWeeks) {
            this.byWeeks[key].totalScore = (
              this.byWeeks[key].kills -
              this.byWeeks[key].teamkills / this.byWeeks[key].totalPlayedGames
            ).toFixed(2);
            this.byWeeks[key].startDate = new Date(
              this.byWeeks[key].startDate
            ).toLocaleString("ru", {
              year: "numeric",
              month: "short",
              day: "numeric",
            });
            this.byWeeks[key].endDate = new Date(
              this.byWeeks[key].endDate
            ).toLocaleString("ru", {
              year: "numeric",
              month: "short",
              day: "numeric",
            });
          }
          if (count > 0) {
            newPlayer.totalPlayedGames = count;
            newPlayer.kills = kills;
            newPlayer.teamkills = teamkills;
            newPlayer.deaths = deaths;
            newPlayer.rotationWeeks = newOne;
            newPlayer.playerName = r.globalStatistics[key].playerName;
            newPlayer.lastSquadPrefix = r.globalStatistics[key].lastSquadPrefix;
            newPlayer.kdRatio = (kills / deaths).toFixed(2);
            newPlayer.totalScore = ((kills - teamkills) / count).toFixed(2);
            this.n[0] = newPlayer;
          } else {
            this.n.push({
              playerName: "Игр за ротацию не найдено",
              lastSquadPrefix: "",
            });
          }
        }
      }
      this.loading = false;
    },
  },
};
</script>
